.two-option-toggle__label {
  display: flex;
  position: relative;
  width: 60px;
  height: 34px;
  justify-content: center;
  margin: 30px auto 50px;
}

.two-option-toggle__first-option,
.two-option-toggle__second-option {
  font-family: @geometria-font;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  color: @color-black;
  width: 115px;
  cursor: pointer;
  display: block;
}

.two-option-toggle__first-option {
  margin-right: 50px;
}

.two-option-toggle__second-option {
  margin-left: 50px;
}


.two-option-toggle__slider {
  position: absolute;
  cursor: pointer;
  top: 15px;
  left: 0;
  background-color: @color-green;
  transition: 0.4s;
  border-radius: 34px;
  width: 54px;
  height: 25px;

  &::before {
    position: absolute;
    content: "";
    height: 22px;
    width: 22px;
    left: 1px;
    bottom: 1px;
    background-color: @color-white;
    transition: 0.4s;
    border-radius: 50%;
  }
}

.two-option-toggle__input:checked + .two-option-toggle__slider::before {
  transform: translateX(29px);
}

.two-option-toggle__first-option:hover,
.two-option-toggle__second-option:hover {
  color: @color-green;
}

@media (min-width: @tablet-width) {
  .two-option-toggle__first-option,
  .two-option-toggle__second-option {
    font-size: 14px;
  }
}

@media (min-width: @desktop-width) {}
